import firestore from '@/firebase/firestore.js'

let docksRef = firestore.collection('decks')

export default {
    getDecks: function () {
        let result = []
        docksRef.get().then(
            function(querySnapshot){
                querySnapshot.forEach(function(doc){
                    result.push(doc.data())
                })
            }
        )
        return result;
    }
}