                >
                    <v-text-field label="声優枠" hint="声優名を記入するところです"></v-text-field>
                </v-col>
            </v-row>
            <v-row dense>
                <v-col cols="12" sm="6" md="4"
                    v-for="item in deck.answertype.circle" :key="item"
                >
